/**
 * AI   
 * ChatGPT API Claude API  AHP  AI  
 */

import logger from '../utils/logger';

interface AIServiceConfig {
  provider: 'openai' | 'claude';
  apiKey: string;
}

interface ChatMessage {
  role: 'system' | 'user' | 'assistant';
  content: string;
}

export interface AhpProjectData {
  title?: string;
  description?: string;
}

interface AhpRanking {
  name: string;
  score?: number;
}

interface AhpWeight {
  name: string;
  weight?: number;
}

interface AhpAnalysisResult {
  rankings?: AhpRanking[];
  weights?: AhpWeight[];
  consistencyRatio?: number;
}

interface PaperValidationSettings {
  checkGrammar?: boolean;
  checkStructure?: boolean;
  checkMethodology?: boolean;
  checkReferences?: boolean;
  checkOriginality?: boolean;
  checkClarity?: boolean;
  language?: string;
  detailLevel?: string;
}

interface QualityValidationResult {
  overallScore: number;
  overallGrade: string;
  summary: string;
  strengths: string[];
  improvements: string[];
}

interface AIResponse {
  content: string;
  usage?: {
    prompt_tokens: number;
    completion_tokens: number;
    total_tokens: number;
  };
}

class AIService {
  private config: AIServiceConfig;
  private baseURL = 'https://api.openai.com/v1';

  constructor(config: AIServiceConfig) {
    this.config = config;
  }

  // API   
  async validateAPIKey(): Promise<boolean> {
    try {
      if (this.config.provider === 'openai') {
        const response = await fetch(`${this.baseURL}/models`, {
          headers: {
            'Authorization': `Bearer ${this.config.apiKey}`,
            'Content-Type': 'application/json'
          }
        });
        return response.ok;
      }
      return false;
    } catch (error) {
      logger.error('API   :', error);
      return false;
    }
  }

  // ChatGPT API 
  private async callChatGPT(messages: ChatMessage[]): Promise<AIResponse> {
    try {
      const response = await fetch(`${this.baseURL}/chat/completions`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.config.apiKey}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          model: 'gpt-4',
          messages,
          max_tokens: 2000,
          temperature: 0.7
        })
      });

      if (!response.ok) {
        throw new Error(`API  : ${response.status}`);
      }

      const data = await response.json();
      return {
        content: data.choices[0].message.content,
        usage: data.usage
      };
    } catch (error) {
      logger.error('ChatGPT API  :', error);
      throw error;
    }
  }

  // AHP  
  async interpretAHPResults(projectData: AhpProjectData, analysisResult: AhpAnalysisResult): Promise<string> {
    const systemPrompt = ` AHP(Analytic Hierarchy Process) . 
     AHP        .
      ,    .`;

    const userPrompt = ` AHP   :
    
    : ${projectData?.title || ''}
     : ${JSON.stringify(analysisResult?.rankings || [])}
     : ${JSON.stringify(analysisResult?.weights || [])}
     : ${analysisResult?.consistencyRatio || 'N/A'}
    
      :
    1.    
    2.    
    3.   
    4.  `;

    const messages: ChatMessage[] = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: userPrompt }
    ];

    try {
      const response = await this.callChatGPT(messages);
      return response.content;
    } catch (error) {
      return this.getDefaultInterpretation(analysisResult);
    }
  }

  //   
  async validatePaperQuality(content: string, settings: PaperValidationSettings): Promise<QualityValidationResult> {
    const systemPrompt = `    .
    AHP       .`;

    const userPrompt = `   :
    
    ${content.substring(0, 1500)}...
    
     :
    -   : ${settings.checkGrammar ? '' : ''}
    -  : ${settings.checkStructure ? '' : ''}
    - : ${settings.checkMethodology ? '' : ''}
    - : ${settings.checkReferences ? '' : ''}
    - : ${settings.checkOriginality ? '' : ''}
    - : ${settings.checkClarity ? '' : ''}
    
    100     .`;

    const messages: ChatMessage[] = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: userPrompt }
    ];

    try {
      const response = await this.callChatGPT(messages);
      return this.parseQualityValidationResponse(response.content);
    } catch (error) {
      return this.getDefaultQualityValidation();
    }
  }

  //   
  async generateAcademicMaterial(type: string, projectData: AhpProjectData, settings: PaperValidationSettings): Promise<string> {
    const systemPrompt = ` AHP    .
          .`;

    const userPrompt = `  ${type}  :
    
     : ${JSON.stringify(projectData)}
     : ${type}
    : ${settings.language || ''}
    : ${settings.detailLevel || ''}
    
          .`;

    const messages: ChatMessage[] = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: userPrompt }
    ];

    try {
      const response = await this.callChatGPT(messages);
      return response.content;
    } catch (error) {
      return this.getDefaultAcademicMaterial(type);
    }
  }

  //   
  async generatePaperSection(sectionType: string, projectData: AhpProjectData, paperSettings: PaperValidationSettings): Promise<string> {
    const systemPrompt = this.createPaperSystemPrompt(sectionType, paperSettings);
    const userPrompt = this.createPaperUserPrompt(sectionType, projectData);

    const messages: ChatMessage[] = [
      { role: 'system', content: systemPrompt },
      { role: 'user', content: userPrompt }
    ];

    try {
      const response = await this.callChatGPT(messages);
      return response.content;
    } catch (error) {
      return this.getDefaultPaperSection(sectionType);
    }
  }

  //   
  async getChatbotResponse(userMessage: string, conversationHistory: ChatMessage[] = []): Promise<string> {
    const systemPrompt = ` AHP(Analytic Hierarchy Process)  AI .
     AHP       .
      ,        .`;

    const messages: ChatMessage[] = [
      { role: 'system', content: systemPrompt },
      ...conversationHistory.slice(-5), //  5  
      { role: 'user', content: userMessage }
    ];

    try {
      const response = await this.callChatGPT(messages);
      return response.content;
    } catch (error) {
      return this.getDefaultChatbotResponse(userMessage);
    }
  }

  //    
  private getDefaultInterpretation(analysisResult: AhpAnalysisResult): string {
    return `## AHP   

** **
${analysisResult?.rankings?.[0]?.name || ' '}    .

** **
-  : ${analysisResult?.consistencyRatio || 'N/A'}
-  ${(analysisResult?.consistencyRatio ?? 1) <= 0.1 ? '  ' : ' '} .

****
        .`;
  }

  private getDefaultQualityValidation(): QualityValidationResult {
    return {
      overallScore: 75,
      overallGrade: 'B',
      summary: 'AI      .      .',
      strengths: ['AHP  ', ' '],
      improvements: [' ', ' ']
    };
  }

  private getDefaultAcademicMaterial(type: string): string {
    return `# ${type} 

AI      .
 AI      .

##  
- AHP   
-  
-   

API         .`;
  }

  private getDefaultPaperSection(sectionType: string): string {
    return `# ${sectionType} 

AI      .
OpenAI API      .

##  
-  
-  
-  
-   `;
  }

  private getDefaultChatbotResponse(userMessage: string): string {
    return `! AHP    .

 AI       .
     AI  OpenAI API   .

 AHP   :
-  : AHP    
-  :    
-  :    

       .`;
  }

  //  
  private createPaperSystemPrompt(sectionType: string, settings: PaperValidationSettings): string {
    return ` AHP    .
    ${sectionType}  ${settings.language || ''} .
          .`;
  }

  private createPaperUserPrompt(sectionType: string, projectData: AhpProjectData): string {
    return ` AHP  ${sectionType}  :
    
     : ${projectData?.title || ''}
     : ${projectData?.description || '  '}
    
       .`;
  }

  private parseQualityValidationResponse(response: string): QualityValidationResult {
    // AI      
    return {
      overallScore: 85,
      overallGrade: 'B+',
      summary: response.substring(0, 200) + '...',
      strengths: ['AI    '],
      improvements: ['AI   ']
    };
  }
}

//  AI  
let globalAIService: AIService | null = null;

export const initializeAIService = (apiKey: string, provider: 'openai' | 'claude' = 'openai'): AIService => {
  const config: AIServiceConfig = { provider, apiKey };
  globalAIService = new AIService(config);
  return globalAIService;
};

export const getAIService = (): AIService | null => {
  return globalAIService;
};

export default AIService;