import React, { useState } from 'react';
import Button from './Button';

interface TrashedProject {
  id: string;
  title: string;
  description: string;
  deleted_at: string;
}

interface TrashOverflowModalProps {
  trashedProjects: TrashedProject[];
  projectToDelete: string;
  onPermanentDelete: (projectId: string) => Promise<void>;
  onCancel: () => void;
  onDeleteAfterCleanup: (projectToDelete: string) => Promise<void>;
}

const TrashOverflowModal: React.FC<TrashOverflowModalProps> = ({
  trashedProjects,
  projectToDelete,
  onPermanentDelete,
  onCancel,
  onDeleteAfterCleanup
}) => {
  const [selectedForDeletion, setSelectedForDeletion] = useState<string>('');
  const [isDeleting, setIsDeleting] = useState(false);
  const [confirmStep, setConfirmStep] = useState(false);
  const [actionMessage, setActionMessage] = useState<{type:'success'|'error'|'info', text:string}|null>(null);

  const showActionMessage = (type: 'success'|'error'|'info', text: string) => {
    setActionMessage({type, text});
    setTimeout(() => setActionMessage(null), 3000);
  };

  const handlePermanentDelete = async () => {
    if (!selectedForDeletion) {
      showActionMessage('error', '   .');
      return;
    }

    if (!confirmStep) {
      setConfirmStep(true);
      return;
    }

    try {
      setIsDeleting(true);
      setConfirmStep(false);

      //    
      await onPermanentDelete(selectedForDeletion);

      //     
      await onDeleteAfterCleanup(projectToDelete);

      showActionMessage('success', '      .');
    } catch (error) {
      showActionMessage('error', '    .');
    } finally {
      setIsDeleting(false);
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center" style={{ backgroundColor: 'rgba(0, 0, 0, 0.5)' }}>
      {actionMessage && (
        <div className={`fixed top-4 right-4 z-[60] px-4 py-3 rounded-lg text-sm font-medium shadow-lg ${actionMessage.type === 'success' ? 'bg-green-100 text-green-800' : actionMessage.type === 'info' ? 'bg-blue-100 text-blue-800' : 'bg-red-100 text-red-800'}`}>
          {actionMessage.text}
        </div>
      )}
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full mx-4 max-h-[80vh] overflow-hidden">
        {/*  */}
        <div className="px-6 py-4 border-b bg-orange-50">
          <div className="flex items-center space-x-3">
            <span className="text-2xl"></span>
            <div>
              <h3 className="text-lg font-bold text-orange-800">  </h3>
              <p className="text-sm text-orange-700">  3  .</p>
            </div>
          </div>
        </div>

        {/*  */}
        <div className="px-6 py-4 space-y-4 overflow-y-auto" style={{ maxHeight: '50vh' }}>
          <div className="bg-red-50 border border-red-200 rounded-lg p-4">
            <h4 className="font-medium text-red-900 mb-2">   </h4>
            <p className="text-sm text-red-700">
                        .
            </p>
          </div>

          <div>
            <h4 className="font-medium text-gray-900 mb-3">   :</h4>
            <div className="space-y-3">
              {trashedProjects.map((project) => (
                <label
                  key={project.id}
                  className={`flex items-center p-3 border rounded-lg cursor-pointer transition-colors ${
                    selectedForDeletion === project.id
                      ? 'border-red-500 bg-red-50'
                      : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
                  }`}
                >
                  <input
                    type="radio"
                    name="projectToDelete"
                    value={project.id}
                    checked={selectedForDeletion === project.id}
                    onChange={(e) => setSelectedForDeletion(e.target.value)}
                    className="mr-3"
                  />
                  <div className="flex-1">
                    <div className="flex items-center justify-between">
                      <h5 className="font-medium text-gray-900">{project.title}</h5>
                      <span className="text-xs text-gray-500">
                        : {formatDate(project.deleted_at)}
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 mt-1">{project.description}</p>
                  </div>
                </label>
              ))}
            </div>
          </div>

          <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
            <h4 className="font-medium text-yellow-900 mb-2"> </h4>
            <ul className="text-sm text-yellow-700 space-y-1">
              <li>      </li>
              <li>   (, ,  )  </li>
              <li>       </li>
            </ul>
          </div>
        </div>

        {/*    */}
        {confirmStep && selectedForDeletion && (() => {
          const project = trashedProjects.find(p => p.id === selectedForDeletion);
          return (
            <div className="px-6 py-3 bg-red-50 border-t border-red-200">
              <p className="text-sm font-medium text-red-800">
                  "{project?.title}"  ?     .
              </p>
            </div>
          );
        })()}

        {/*   */}
        <div className="px-6 py-4 border-t bg-gray-50 flex justify-end space-x-3">
          <Button
            variant="secondary"
            onClick={() => { setConfirmStep(false); onCancel(); }}
            disabled={isDeleting}
          >
            
          </Button>
          {confirmStep && (
            <Button
              variant="secondary"
              onClick={() => setConfirmStep(false)}
              disabled={isDeleting}
            >
              
            </Button>
          )}
          <Button
            variant="error"
            onClick={handlePermanentDelete}
            disabled={!selectedForDeletion || isDeleting}
            loading={isDeleting}
          >
            {isDeleting ? ' ...' : confirmStep ? '    ' : '   '}
          </Button>
        </div>
      </div>
    </div>
  );
};

export default TrashOverflowModal;