// CSV   
export const exportToCSV = () => {
  //   CSV
  const criteriaCSV = [
    ['ID', '', '', '', ''],
    ...aiDevelopmentCriteria.map(c => [
      c.id,
      c.category,
      c.name,
      c.description,
      c.weight?.toString() || ''
    ])
  ];

  //  CSV
  const alternativesCSV = [
    ['ID', '', '', '', '', ''],
    ...aiDevelopmentAlternatives.map(a => [
      a.id,
      a.name,
      a.company,
      a.description,
      a.features.join('; '),
      a.pricing
    ])
  ];

  //    CSV
  const scoresCSV = [
    ['/', ...aiDevelopmentCriteria.map(c => c.name)],
    ...aiDevelopmentAlternatives.map(a => [
      a.name,
      ...aiDevelopmentCriteria.map(c => a.scores?.[c.id]?.toString() || '0')
    ])
  ];

  return {
    criteria: criteriaCSV,
    alternatives: alternativesCSV,
    scores: scoresCSV
  };
};

