export const ERROR_MESSAGES = {
  NETWORK_ERROR: '  .',
  UNAUTHORIZED: ' .  .',
  SERVER_ERROR: '  .    .',
  TIMEOUT: '  .',
  UNKNOWN_ERROR: '    .'
} as const;