import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IconPrefix, IconName } from '@fortawesome/fontawesome-svg-core';
import { emojiToFontAwesome } from '../../utils/fontAwesome';

// UI    
export const UIColors = {
  primary: '#3B82F6',     //  -  
  secondary: '#6B7280',   //  -  
  success: '#10B981',     //  - /
  warning: '#F59E0B',     //  - /
  danger: '#EF4444',      //  - /
  info: '#3B82F6',        // 
  muted: '#9CA3AF',       //   - 
  text: '#111827',        // 
  background: '#F9FAFB'   // 
} as const;

//   
export const UIIconSizes = {
  xs: '0.75rem',     // 12px
  sm: '0.875rem',    // 14px  
  base: '1rem',      // 16px
  lg: '1.125rem',    // 18px
  xl: '1.25rem',     // 20px
  '2xl': '1.5rem',   // 24px
  '3xl': '1.875rem', // 30px
  '4xl': '2.25rem',  // 36px
  '5xl': '3rem'      // 48px
} as const;

//    
export const UIIconPresets = {
  button: {
    size: 'base' as keyof typeof UIIconSizes,
    className: 'transition-colors duration-200'
  },
  navigation: {
    size: 'lg' as keyof typeof UIIconSizes,
    className: 'transition-all duration-200'
  },
  status: {
    size: 'sm' as keyof typeof UIIconSizes,
    className: 'inline-block'
  },
  hero: {
    size: '4xl' as keyof typeof UIIconSizes,
    className: 'drop-shadow-lg'
  },
  card: {
    size: '2xl' as keyof typeof UIIconSizes,
    className: 'mb-3'
  }
} as const;

interface UIIconProps {
  emoji?: string;
  icon?: string;
  preset?: keyof typeof UIIconPresets;
  size?: keyof typeof UIIconSizes;
  color?: keyof typeof UIColors | string;
  className?: string;
  hover?: boolean;
  active?: boolean;
  disabled?: boolean;
  ariaLabel?: string;
  onClick?: () => void;
}

export const UIIcon: React.FC<UIIconProps> = ({
  emoji,
  icon,
  preset,
  size = 'base',
  color = 'text',
  className = '',
  hover = false,
  active = false,
  disabled = false,
  ariaLabel,
  onClick
}) => {
  //    
  const appliedSize = preset ? UIIconPresets[preset].size : size;
  const presetClassName = preset ? UIIconPresets[preset].className : '';
  
  //   
  let iconName: string | null = null;
  
  if (emoji && emojiToFontAwesome[emoji as keyof typeof emojiToFontAwesome]) {
    iconName = emojiToFontAwesome[emoji as keyof typeof emojiToFontAwesome];
  } else if (icon) {
    iconName = icon;
  }
  
  //  
  const colorValue = UIColors[color as keyof typeof UIColors] || color;
  
  //   
  const combinedClassName = [
    presetClassName,
    className,
    hover && 'hover:opacity-80 cursor-pointer',
    active && 'opacity-90',
    disabled && 'opacity-50 cursor-not-allowed',
    onClick && 'cursor-pointer'
  ].filter(Boolean).join(' ');
  
  //  
  const iconStyle: React.CSSProperties = {
    color: colorValue,
    fontSize: UIIconSizes[appliedSize],
    transition: 'all 0.2s ease-in-out'
  };
  
  // Font Awesome   
  if (iconName) {
    return (
      <FontAwesomeIcon
        icon={['fas', iconName as IconName] as [IconPrefix, IconName]}
        className={combinedClassName}
        style={iconStyle as any /* FA CSSVariables index sig incompatible with CSSProperties */}
        aria-label={ariaLabel}
        onClick={onClick}
        role={onClick ? 'button' : undefined}
        tabIndex={onClick ? 0 : undefined}
        onKeyDown={onClick ? (e) => {
          if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault();
            onClick();
          }
        } : undefined}
      />
    );
  }
  
  //  
  if (emoji) {
    return (
      <span
        className={combinedClassName}
        style={iconStyle as React.CSSProperties}
        aria-label={ariaLabel}
        onClick={onClick}
        role={onClick ? 'button' : undefined}
        tabIndex={onClick ? 0 : undefined}
        onKeyDown={onClick ? (e) => {
          if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault();
            onClick();
          }
        } : undefined}
      >
        {emoji}
      </span>
    );
  }
  
  //   null 
  return null;
};

//     shorthand 
export const SearchIcon: React.FC<Omit<UIIconProps, 'emoji'>> = (props) => (
  <UIIcon emoji="" ariaLabel="" {...props} />
);

export const EditIcon: React.FC<Omit<UIIconProps, 'emoji'>> = (props) => (
  <UIIcon emoji="" ariaLabel="" {...props} />
);

export const DeleteIcon: React.FC<Omit<UIIconProps, 'emoji'>> = (props) => (
  <UIIcon emoji="" ariaLabel="" color="danger" {...props} />
);

export const SaveIcon: React.FC<Omit<UIIconProps, 'emoji'>> = (props) => (
  <UIIcon emoji="" ariaLabel="" color="success" {...props} />
);

export const AddIcon: React.FC<Omit<UIIconProps, 'emoji'>> = (props) => (
  <UIIcon emoji="" ariaLabel="" color="primary" {...props} />
);

export const SettingsIcon: React.FC<Omit<UIIconProps, 'emoji'>> = (props) => (
  <UIIcon emoji="" ariaLabel="" color="secondary" {...props} />
);

export default React.memo(UIIcon);