// API   
export interface ApiResponse<T = unknown> {
  success: boolean;
  data?: T;
  message?: string;
  error?: string;
}

// Django API    (   )
export interface DjangoProjectResponse {
  id: string;
  title: string;
  description: string;
  objective?: string;
  status: 'draft' | 'active' | 'completed' | 'deleted' | 'evaluation' | 'archived';
  evaluation_mode: 'practical' | 'theoretical' | 'direct_input' | 'fuzzy_ahp';
  workflow_stage: 'creating' | 'waiting' | 'evaluating' | 'completed';
  created_at: string;
  updated_at: string;
  deleted_at?: string;
  deadline?: string;
  criteria_count: number;
  alternatives_count: number;
  member_count?: number;
  owner?: string;
  tags?: string[];
  settings?: Record<string, unknown>; //   
}

