// Django REST Framework   
interface DjangoPaginatedResponse<T> {
  count?: number;
  next?: string | null;
  previous?: string | null;
  results?: T[];
}

// Django   (  )
type DjangoCriteriaResponse = DjangoPaginatedResponse<CriteriaData> | CriteriaData[];

// Django   
type DjangoProjectListResponse = DjangoPaginatedResponse<DjangoProjectResponse> | DjangoProjectResponse[];

// AHP   
interface AnalysisResult {
  weights?: Record<string, number>;
  consistency_ratio?: number;
  rankings?: Array<{ name: string; score: number }>;
  [key: string]: unknown;
}

// HTTP   
const getAuthHeaders = (): HeadersInit => {
  const headers: HeadersInit = {
    'Content-Type': 'application/json',
  };
  
  const accessToken = sessionStorage.getItem('ahp_access_token');
  if (accessToken) {
    headers['Authorization'] = `Bearer ${accessToken}`;
  }
  
  return headers;
};

// API   
const makeRequest = async <T>(
  endpoint: string, 
  options: RequestInit = {}
): Promise<ApiResponse<T>> => {
  try {
    const url = `${API_BASE_URL}${endpoint}`;
    
    const response = await fetch(url, {
      credentials: 'include',
      ...options,
      headers: {
        ...getAuthHeaders(),
        ...options.headers
      }
    });
    
    // DELETE       
    const isDeleteRequest = options.method?.toUpperCase() === 'DELETE';
    
    //  JSON   
    const contentType = response.headers.get('content-type');
    let data: Record<string, unknown> | null = null;

    if (!contentType || !contentType.includes('application/json')) {
      if (isDeleteRequest && response.ok) {
        // DELETE   JSON   (: 204 No Content)
        return {
          success: true,
          data: undefined,
          message: ' '
        };
      } else {
        throw new Error(`    . (${response.status})`);
      }
    } else {
      data = await response.json() as Record<string, unknown>;
    }

    if (!response.ok) {
      // 405 Method Not Allowed  
      if (response.status === 405) {
        // 405      
        return {
          success: false,
          error: `${endpoint}  ${options.method}   .   .`,
          message: 'Method Not Allowed'
        };
      }

      // 500    -     
      if (response.status === 500) {
        const errorDetail = (data?.detail || data?.error || data?.message || '  ') as string;
        throw new Error(` : ${errorDetail}`);
      }

      // 401 Unauthorized 
      if (response.status === 401) {
        throw new Error(' .   .');
      }

      //   
      if (response.status === 403) {
        throw new Error('    .   .');
      }

      // 400   
      if (response.status === 400) {
        const errorMessage = (data?.message || data?.error ||
                           (typeof data === 'object' ? JSON.stringify(data) : 'Bad Request')) as string;
        throw new Error(` : ${errorMessage}`);
      }

      // 404 Not Found 
      if (response.status === 404) {
        throw new Error(`   : ${endpoint}`);
      }

      throw new Error((data?.message || data?.error || `HTTP ${response.status}: API  `) as string);
    }

    return {
      success: true,
      data: (data?.data ?? data) as T,
      message: data?.message as string | undefined
    };
  } catch (error: unknown) {
    logger.error(`API Error [${endpoint}]:`, error);
    //   ( , CORS,  )    
    let errorMessage = (error instanceof Error ? error.message : null) || '    .';
    if (errorMessage === 'Failed to fetch' || errorMessage.includes('NetworkError') || errorMessage.includes('net::ERR')) {
      errorMessage = '   .       .';
    }
    return {
      success: false,
      error: errorMessage
    };
  }
};

// ===  API ===
export const projectApi = {
  //    ( )
  getProjects: async () => {
    const response = await makeRequest<DjangoProjectListResponse>(API_ENDPOINTS.PROJECTS.LIST);

    if (response.success) {
      // response.data undefined    
      if (!response.data) {
        return {
          success: true,
          data: [],
          message: ' '
        };
      }

      // Django   
      const normalizedProjects = normalizeProjectListResponse(response.data);
      return {
        success: true,
        data: normalizedProjects,
        message: response.message
      };
    }
    return { success: false, error: response.error } as ApiResponse<ProjectData[]>;
  },

  //    ( )
  getProject: async (id: string) => {
    const response = await makeRequest<DjangoProjectResponse>(API_ENDPOINTS.PROJECTS.GET(id));
    if (response.success && response.data) {
      // Django   
      const normalizedProject = normalizeProjectData(response.data);
      return {
        success: true,
        data: normalizedProject,
        message: response.message
      };
    }
    return { success: false, error: response.error } as ApiResponse<ProjectData>;
  },

  //   ( )
  createProject: async (data: Omit<ProjectData, 'id'>) => {
    //   Django  
    // Django     
    const djangoData = {
      title: data.title,
      description: data.description,
      objective: data.objective || '',
      status: 'draft', // Django : draft, in_progress, completed, archived, deleted
      evaluation_mode: data.evaluation_mode || 'practical',
      workflow_stage: 'creating', // Django : creating, waiting, evaluating, completed
    };

    const response = await makeRequest<DjangoProjectResponse>(API_ENDPOINTS.PROJECTS.CREATE, {
      method: 'POST',
      body: JSON.stringify(djangoData)
    });

    if (response.success && response.data) {
      // Django   
      const normalizedProject = normalizeProjectData(response.data);
      return {
        success: true,
        data: normalizedProject,
        message: response.message
      };
    }
    return { success: false, error: response.error } as ApiResponse<ProjectData>;
  },

  //   ( )
  updateProject: async (id: string, data: Partial<ProjectData>) => {
    //   Django  
    const djangoData: Record<string, unknown> = {};
    if (data.title !== undefined) djangoData.title = data.title;
    if (data.description !== undefined) djangoData.description = data.description;
    if (data.objective !== undefined) djangoData.objective = data.objective;
    if (data.status !== undefined) djangoData.status = data.status;
    if (data.evaluation_mode !== undefined) djangoData.evaluation_mode = data.evaluation_mode;
    if (data.workflow_stage !== undefined) djangoData.workflow_stage = data.workflow_stage;
    if (data.dueDate !== undefined) djangoData.deadline = data.dueDate; // dueDate  deadline 
    if (data.settings !== undefined) djangoData.settings = data.settings; // settings  

    const response = await makeRequest<DjangoProjectResponse>(`/api/service/projects/projects/${id}/`, {
      method: 'PATCH',
      body: JSON.stringify(djangoData)
    });

    if (response.success && response.data) {
      // Django   
      const normalizedProject = normalizeProjectData(response.data);
      return {
        success: true,
        data: normalizedProject,
        message: response.message
      };
    }
    return { success: false, error: response.error } as ApiResponse<ProjectData>;
  },

  //   ( )
  deleteProject: (id: string) =>
    makeRequest<void>(API_ENDPOINTS.PROJECTS.DELETE(id), {
      method: 'DELETE'
    }),

  //    ( ) -     
  getTrashedProjects: async () => {
    const response = await makeRequest<{count: number, results: DjangoProjectResponse[]}>('/api/service/projects/');
    if (response.success && response.data) {
      // Django   
      const normalizedProjects = normalizeProjectListResponse(response.data);
      return {
        success: true,
        data: normalizedProjects,
        message: response.message
      };
    }
    return { success: false, error: response.error } as ApiResponse<ProjectData[]>;
  },

  //   -   
  restoreProject: (id: string) =>
    makeRequest<void>(`/api/service/projects/${id}/`, {
      method: 'PATCH',
      body: JSON.stringify({ is_active: true })
    }),

  //    -   
  permanentDeleteProject: (id: string) =>
    makeRequest<void>(`/api/service/projects/${id}/`, {
      method: 'DELETE'
    })
};

// ===  API ===
export const criteriaApi = {
  //    
  getCriteria: async (projectId: string) => {
    // CriteriaViewSet project   
    const response = await makeRequest<DjangoCriteriaResponse>(`/api/service/projects/criteria/?project=${projectId}`);

    if (response.success && response.data) {
      // Django REST Framework  
      let criteriaList: CriteriaData[] = [];
      const data = response.data as Record<string, unknown>;
      if (Array.isArray(response.data)) {
        //   
        criteriaList = response.data;
      } else if (Array.isArray(data.results)) {
        //    
        criteriaList = data.results as CriteriaData[];
      } else if (Array.isArray(data.data)) {
        // data     
        criteriaList = data.data as CriteriaData[];
      }
      
      return {
        success: true,
        data: criteriaList,
        error: undefined
      };
    }
    
    return {
      success: false,
      data: [],
      error: response.error || '  .'
    };
  },

  //   - PostgreSQL DB 
  createCriteria: async (data: Omit<CriteriaData, 'id'>) => {
    const projectId = data.project_id; // UUID 
    
    if (!projectId || typeof projectId !== 'string') {
      return {
        success: false,
        error: ' ID  .'
      };
    }
    
    // parent_id  -  ID  UUID  
    const parentId = data.parent || data.parent_id || null;
    
    // UUID   
    const isValidUUID = (uuid: string): boolean => {
      const uuidRegex = /^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i;
      return uuidRegex.test(uuid);
    };
    
    // temp_   ID 
    const isTempId = (id: string): boolean => {
      return /^(temp_|criterion_)\d+/.test(id);
    };
    
    //  ID  
    const isNumericId = (id: string): boolean => {
      return /^\d+$/.test(id);
    };
    
    // parent_id  -  ID, UUID,  ID  
    let validParentId = null;
    if (parentId) {
      const parentIdString = String(parentId);
      
      if (isTempId(parentIdString)) {
        validParentId = null;
      } else if (isValidUUID(parentIdString) || isNumericId(parentIdString)) {
        // UUID  ID   
        validParentId = parentIdString;
      } else {
        validParentId = null;
      }
    }
    
    // Django CriteriaViewSet API 
    const requestData = {
      project: projectId,  // Django ForeignKey expects project ID
      name: data.name,
      description: data.description || '',
      type: data.type || 'criteria',
      //  parent_id 
      parent: validParentId,
      parent_id: validParentId,
      order: data.order || 0,
      position: data.position || data.order || 0,
      level: data.level || 1,
      weight: data.weight || 0.0,
      is_active: true
    };
    
    // CriteriaViewSet create endpoint 
    const response = await makeRequest<CriteriaData>('/api/service/projects/criteria/', {
      method: 'POST',
      body: JSON.stringify(requestData)
    });
    
    if (response.success) {
      return response;
    }

    return response;
  },

  //  
  updateCriteria: (id: string, data: Partial<CriteriaData>) =>
    makeRequest<CriteriaData>(`/api/service/projects/criteria/${id}/`, {
      method: 'PUT',
      body: JSON.stringify(data)
    }),

  //  
  deleteCriteria: async (criteriaId: string, _projectId?: string) => {
    const response = await makeRequest<void>(`/api/service/projects/criteria/${criteriaId}/`, {
      method: 'DELETE'
    });
    return response;
  },

  //   
  reorderCriteria: (projectId: string, criteriaIds: string[]) =>
    makeRequest<void>(`/api/service/projects/${projectId}/criteria/reorder/`, {
      method: 'PUT',
      body: JSON.stringify({ criteriaIds })
    })
};

